import React, { useState } from 'react';
import { Button,Form } from "react-bootstrap";
import '../../styles/login/loginForm.css';
import { setAuthToken, tokenUserInfo } from "../../global/auth";

import Kakao from '../../img/login/kakao.svg';
import Google from '../../img/login/google.svg';
import Naver from '../../img/login/naver.svg';
import cUser from "../../img/register/c-user.svg";
import cPassword from "../../img/register/c-password.svg";

function LoginForm() {

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [errorMessage, setErrorMessage] = useState('');


    const handleLogin = () => {
      fetch('/api/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          member_email: email,
          member_pwd: password,
        }),
      })
      .then((response) => {
          if (response.status === 200) {

            return response.text(); //  response.json()  JSON 
          } else if (response.status === 401) {
            //   (UNAUTHORIZED)
            throw new Error('  ');
            
          } else {
            //   
            throw new Error(' ');
          }
        })
        .then((data) => {
          //   (data) 
          const token = JSON.parse(data).token;
          console.log(token);
          const refreshToken = JSON.parse(data).refreshToken;
          const decodedToken = tokenUserInfo(token);

          setAuthToken(token,decodedToken.exp, refreshToken);

          //  
          window.location.reload();

        })
        .catch((error) => {
          //  
          console.error(' :', error.message);
          setErrorMessage(error.message);
          //        
        });
    };

    return (
<>
<div className="login-main-content">
    <div className="gnb-log-main-img" />
    <div className="custom-form-group">
        <div className='c-f-g-login-component-top'
        style={{backgroundImage: `url(${cUser})`, 
        backgroundRepeat: 'no-repeat',
        backgroundPosition: '10px',
        backgroundSize: '20px'
        }}>
      <Form.Control 
        type="text"
        placeholder=""
        value={email}
        onChange={(e) => setEmail(e.target.value)} 
      />
      </div>
      <div className='c-f-g-login-component-bottom'
        style={{backgroundImage: `url(${cPassword})`, 
        backgroundRepeat: 'no-repeat',
        backgroundPosition: '10px',
        backgroundSize: '20px'
        }}>
      <Form.Control 
        type="password"
        placeholder=""
        value={password}
        onChange={(e) => setPassword(e.target.value)} 
      />
      </div>
    </div>
    <div className="content-item-button">
      <Button
        className="login-button"
        onClick={handleLogin}
        style={{ width: '235px' }}
      >
        
      </Button>
    </div>
    <div>
      <span className="login-error">{errorMessage}</span>
    </div>
    <p className="find-account">
      <a className="find-account-link" href="#"> </a>
      <span className="find-account-span" />
      <a className="find-account-link" href="#"> </a>
      <span className="find-account-span" />
      <a className="find-account-link" href="#"></a>
    </p>
    <div className="hr-form-span">
      <hr />
      <span> </span>
      <div className="fast-login-wrapper">
        <div>
          <a href="http://localhost:8080/oauth2/authorization/kakao">
            <img alt="" src={Kakao} />
          </a>
        </div>
        <div>
          <a href="http://localhost:8080/oauth2/authorization/google">
            <img alt="" src={Google} />
          </a>
        </div>
        <div>
          <img alt="" src={Naver} />
        </div>
      </div>
    </div>
  </div>

</>

    );
}

export default LoginForm;
